function userReducer (state, action) {
  switch (action.type) {
    case 'LOGIN':
    case 'REGISTER':
      return {
        'username': action.username,
        'access_token': action.access_token
      }
    case 'LOGOUT':
      return {
        'username': undefined,
        'access_token': undefined
      }
    default:
      return state;
  }
}

function usersReducer (state, action) {
  switch (action.type) {
    case 'FETCH_USERS':
      return action.users
    default:
      return state;
  }
}

function todosReducer (state, action) {
  console.log('tr',state,action);
  switch (action.type) {
    case 'CREATE_TODO':
      const newTodo = { 
        id: action.id,
        title: action.title,
        description: action.description, 
        author: action.author,
        completed: false,
        dateCompleted: -1,
        hidden: false
      }
      return [ newTodo, ...state ]
    case 'TOGGLE_TODO':
      return state.map((p) => {
        if(p._id === action.todoId) {
          p.completed = action.completed;
          p.dateCompleted = action.dateCompleted;
        }
        return p;
      })
    case 'DELETE_TODO':
      return state.filter((p) => p._id !== action.todoId);
    case 'FETCH_TODOS':
      return action.todos;
    case 'FETCH_TODO':
      return action.todo;
    default:
      return state;
  }
}

export default function appReducer (state, action) {
  return {
    user: userReducer(state.user, action),
    todos: todosReducer(state.todos, action),
    users: usersReducer(state.users, action),
  }
}
