import React, {useContext, useEffect} from 'react'
import { Link } from 'react-navi'
import { Card, Button } from 'react-bootstrap'

import { ThemeContext, StateContext } from './Contexts'

import { useResource } from 'react-request-hook'

import { useNavigation } from 'react-navi'


function Todo({ title, description, author, completed, dateCompleted,
	todoId, short = false, _id }) {

  if (todoId == undefined) todoId = _id;
  const {secondaryColor} = useContext(ThemeContext)
  const { state, dispatch } = useContext(StateContext)
  const navigation = useNavigation()

  const [deletedTodo, deleteTodo] = useResource((todoId) => ({
    url: `/todo/${todoId}`,
    method: "delete",
    headers: {"Authorization": `${state.user.access_token}`},
  }));

  const [toggledTodo, toggleTodo] = useResource((todoId, completed) => ({
    url: `/todo/${todoId}/${completed}`,
    method: "patch",
    headers: {"Authorization": `${state.user.access_token}`},
  }));

  useEffect(() => {
    if (deletedTodo && deletedTodo.data) {
      // auth?
      if (deletedTodo.data.deletedCount == 1) {
        dispatch({type: 'DELETE_TODO', todoId: todoId});
        if (document.location.href.indexOf('/todo/') != -1)
        navigation.navigate(`/`);
      } else {
        alert('Unauthorized!');
      }
    } 
  }, [deletedTodo])

  useEffect(() => {
    if (toggledTodo && toggledTodo.data && toggledTodo.isLoading === false) {
      // auth?
      if (toggledTodo.data.updateCount.modifiedCount == 1) {
        dispatch({type: 'TOGGLE_TODO',
          completed: toggledTodo.data.completed,
          dateCompleted: toggledTodo.data.dateCompleted,
          todoId
        })
        if (document.location.href.indexOf('/todo/') != -1)
          navigation.navigate(`/`);
      } else {
        alert('Unauthorized!');
      }
    }
  }, [toggledTodo])

  let processed = description

  if (short) {
    if (description.length > 30) {
      processed = description.substring(0, 30) + '...'
    }
  }


  return (
    <Card>
      <Card.Body>
        <Card.Title>
          <Link style={{ color: secondaryColor }} href={`/todo/${todoId}`}>{title}</Link>
        </Card.Title>
        <Card.Subtitle>
          <i>Written by <b>{author}</b></i>
        </Card.Subtitle>
        <Card.Text>
          {processed}
        </Card.Text>

        <input type="checkbox" checked={completed} onChange={e => {toggleTodo(todoId, e.target.checked)}} />
        
        <Button variant="link" onClick={(e) => {deleteTodo(todoId)}}>Delete Todo</Button>

        {completed && <i>Completed on: {new Date(dateCompleted).toLocaleDateString('en-us')}</i>}

        {short && <Link href={`/todo/${todoId}`}>View full todo</Link>}

      </Card.Body>
    </Card>

  )
}

export default React.memo(Todo);
